
.status-box {
  position: relative;
}
.status-box.top {
  margin-top: 10px;
}
.status-box.bottom {
  margin-bottom: 10px;
}
.status-box.right {
  margin-right: 10px;
}
.status-box.left {
  margin-left: 10px;
}
.status-box .status-button {
  height: 88px;
  width: 100px;
}
.status-box .status-arrow {
  content: '';
  position: absolute;
  border: 10px solid transparent;
}
.status-box.right .status-arrow {
  top: 34px;
  right: -20px;
  border-left-color: #F05B47;
}
.status-box.left .status-arrow {
  top: 34px;
  left: -20px;
  border-right-color: #F05B47;
}
.status-box.top .status-arrow {
  top: -20px;
  left: 40px;
  border-bottom-color: #F05B47;
}
.status-box.bottom .status-arrow {
  bottom: -20px;
  left: 40px;
  border-top-color: #F05B47;
}
.status-box .status-button-icon {
  font-size: 36px;
}
.status-box .mu-flat-button-wrapper {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.status-box .mu-icon {
  margin: 0;
}
.status-box .mu-flat-button-label {
  padding: 0;
}
/*.status-box {
  width: 100px;
  height: 88px;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.95);
}*/
.status-box.row {
  margin-right: 2px;
}
.status-box.column {
  margin-bottom: 2px;
}
.status-box .head-status-number {
  margin: 10px 0 0 0;
  font-size: 20px;
  line-height: 24px;
  font-weight: normal;
}
.status-box.clickable .head-status-number {
  color: #F05B47;
}
