
html, body {
  overflow-x: auto;
  overflow-y: hidden;
}
.setting-appbar.mu-appbar {
  height: 74px;
  padding-left: 100px;
  background-color: #00bcd4;
}
.setting-dropdown {
  line-height: 46px;
  margin-right: 48px;
}
.setting-dropdown label {
  vertical-align: text-bottom;
}
.setting-dropdown .mu-dropDown-menu-text {
  color: #fff;
}
#yearDropDown {
  margin-right: -34px;
}
.settle-table .table-header {
  background-color: #eee;
}
.settle-table .table-header .mu-th {
  padding: 0;
  color: #333;
  border-bottom: 1px solid #c7c7c7;
  text-align: center;
}
.settle-table .mu-th-wrapper {
  white-space: pre-wrap;
}
.settle-table .mu-td {
  font-size: 16px;
  padding: 1em;
  white-space: pre-wrap;
  text-align: center;
  word-wrap: break-word;
  word-break: break-all;
}
.settle-table .detail-btn {
  min-width: 60px;
  line-height: 2;
  height: 2em;
  font-size: 14px;
  margin: 0;
  white-space: normal;
}
.settle-table .worker-td-0 {
  display: none;
}
.modal-popup {
  width: 1200px;
  max-width: 1200px;
}
.modal-popup {
  width: 1200px;
  max-width: 1200px;
}



